} */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 900px) {
  body {
    display: block;
  }
}

.main-container
{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
}

/*GENERAL BACKGROUND*/
body
{
  background: rgba(70, 104, 196, 0.752);
  overflow-y:hidden;
}

/* FONTS */
@font-face
{
  font-family: "Avenir Book";
  src: url(/static/media/AvenirLTStd-Book.ecb0c2ae.otf);
}

@font-face
{
  font-family: "Avenir Roman";
  src: url(/static/media/AvenirLTStd-Roman.b1d7c6e0.otf);
}

@font-face
{
  font-family: "Avenir Medium";
  src: url(/static/media/AvenirLTStd-Medium.4f995fa4.otf);
}

@font-face
{
  font-family: "8 bit";
  src: url(/static/media/8-bit-pusab.0288f1e7.ttf);
}

@font-face
{
  font-family: "Chicago";
  src: url(/static/media/ChicagoFLF.d7982e4c.ttf);
}
.bottom-container
{
  flex-direction: column;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background: rgb(210, 210, 210);
}

.bottom-menu-container
{
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

.bottom-menu-container ul
{
  padding: 0;
  list-style-type: none;
}​

.bottom-menu
{
  border-style: outset;
}

.bottom-menu-item i
{
  color: rgb(0, 0, 0);
  padding: 8px 8px;
  background: rgb(219, 219, 219);
  border-color: rgb(139, 139, 139);
  border-width: 4px;
  border-style: outset;
  -webkit-transition: 0.2s all ease;
  transition: 0.2s all ease;
}

.bottom-menu-item i:hover
{
  background-color: rgb(152, 152, 152);
}

@media only screen and (max-height: 1000px)
{
  .bottom-menu-item i
  {
    color: rgb(0, 0, 0);
    padding: 4px 4px;
    background: rgb(219, 219, 219);
    border-color: rgb(169, 169, 169);
    border-width: 4px;
    border-style: outset;
    -webkit-transition: 0.2s all ease;
    transition: 0.2s all ease;
  }
}
.profile-page
{
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-position: center;
}

.rectangle
{
  background: -webkit-linear-gradient(rgba(0,0,0,0.95), rgba(0,0,0,0.95)), no-repeat center;
  background: linear-gradient(rgba(0,0,0,0.95), rgba(0,0,0,0.95)), no-repeat center;
}

.profile-name h1
{
  margin-top: 40px;
  margin-left: 5px;
  font-size: 47px;
  font-style: oblique;
  color: rgb(0, 0, 0);
  text-shadow: 3px 3px rgb(198, 198, 198);
  font-weight: bold;
  font-family: "Chicago";
}

.profile-description h1
{
  margin-top: 20px;
  margin-bottom: 0px;
  width: 100%;
  font-family: "Chicago";
  color:rgb(0, 0, 0);
  font-size: 22px;
  text-overflow: ellipsis;
}

#cursor {
  border-left: .8em solid rgba(110, 110, 110, 0.676);
  padding-top: 7px;
}

@-webkit-keyframes blink {
  50% {
    border-color: transparent;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

@media only screen and (max-width: 600px)
{
  .profile-name h1
  {
    font-size: 27px;
    text-shadow: 2px 2px rgb(164, 164, 164);
  }

  .profile-description h1
  {
    font-size: 15px;
  }
}


.main-nav
{
  display: flex;
  flex-direction: column;
}

.main-nav ul
{
  padding: 0;
  margin-top: 0px;
}

.nav-menu 
{
  list-style-type: none;
  display: flex;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  flex-direction: column;
  justify-content: center;
}

.home-nav-menu .nav-item
{
  color: rgb(0, 0, 0);
  font-family: "Chicago";
  text-shadow: 1px 1px rgb(193, 193, 193);
  text-decoration: underline;
  font-style: italic;
  font-size: 22px;
  margin-top: 40px;
  margin-left: 8px;
  cursor: pointer;
  overflow: hidden;
  -webkit-transition: 0.2s all ease;
  transition: 0.2s all ease;
}

.home-nav-menu .nav-item:hover
{
  background-color: rgb(212, 212, 212);
}

@media only screen and (max-width: 600px)
{
  .home-nav-menu .nav-item
  {
    font-size: 18px;
    margin-top: 30px;
    text-shadow: 1px 1px rgb(193, 193, 193);
  }
}
.topbar-container
{
  flex-direction: column;
  display: flex;
  margin-top: 0px;
  width: 100%;
