  list-style-type: none;
}

.topbar-menu-container li a
{
  color: rgb(0, 0, 0);
  text-decoration: none;
  font-family: "Chicago";
  font-size: 19px;
  margin: 13px;
  overflow: hidden;
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
}

.topbar-menu-container li a:hover
{
  color: rgb(40, 40, 40);
}

.topbar-menu
{
  list-style-type: none;
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 500px)
{
